/* SIDEBAR */
.sidebar {
	background: white;
	border-right: 1px solid #eaedf3;
	overflow-y: auto;
	-webkit-overflow-scrolling: touch;
}

/* SECTION */
.sidebar-section:first-child {
	margin-top: 15px;
}

/* TITLE */
.sidebar-section__title {
	text-transform: uppercase;
	font-weight: 500;
	font-size: 0.75rem;
	color: #9ea0a5;
	line-height: 1.5;
	display: block;
	padding: 10px 30px;
}

.top-bar__title {
	font-weight: 500;
	font-size: 1rem;
	color: #3e3f42;
	line-height: 1.75;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	display: inline-flex;
}

/* LIST */
.sidebar-link {
	font-size: 0.875rem;
	color: #3e3f42;
	display: block;
	padding: 0 30px;
	min-height: 40px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	-webkit-box-pack: justify;
	-ms-flex-pack: justify;
	justify-content: space-between;
	-webkit-transition: 0.5s;
	transition: 0.5s;
}

.sidebar-link .sidebar-link__number {
	font-size: 0.75rem;
	color: #9ea0a5;
	line-height: 1rem;
}

.sidebar-link.active {
	background: #eaedf3;
}

.sidebar-link:hover {
	background: #eaedf3;
	-webkit-transition: 0.5s;
	transition: 0.5s;
}

.logo {
	justify-content: center;
	display: inline-flex;
	width: 100%;
	margin: 20px 0;
	padding: 0 0 15px 0;
	border-bottom: 1px solid #eaedf3;
	-webkit-transition: 0.5s;
	transition: 0.5s;
}

.top-bar__title:hover {
	background: linear-gradient(45deg, #fe6b8b 30%, #ff8e53 90%);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	-webkit-transition: 0.5s;
	transition: 0.5s;
}

.top-bar__title:hover .hot {
	background: #3e3f42;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	-webkit-transition: 0.5s;
	transition: 0.5s;
}

.version {
	color: #818b9e;
	font-size: 11px;
	margin-left: 2px;
	margin-top: 3px;
	display: inline-flex;
	vertical-align: top;
}

.hot {
	background: linear-gradient(45deg, #fe6b8b 30%, #ff8e53 90%);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	font-size: 35px;
	transform: rotate(10deg);
}

.burger {
	position: absolute;
	top: 26px;
	left: 30px;
}

@media all and (max-width: 959px) {
	.categories,
	.account {
		display: none;
	}
}

@media all and (min-width: 960px) {
	.burger {
		display: none;
	}
}
